.page {
  font-family: "Poppins", sans-serif;

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(+100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__main {
    margin: 0;
    padding: 0;

    background-color: $colorMenuText;
    background-size: cover;
  }
}
